
.cw-gantt {
  border-collapse: collapse;
}
.cw-gantt td {
  border: 1px solid #ccc;
}
.cw-gantt-task {
  border: 1px solid #ccc;
}
.cw-gantt-table {
}
.cw-gantt-month {
  border: 1px solid #ccc;
}
.cw-gantt-day {
  border: 1px solid #ccc;
}
.cw-gantt-wrapper {
  font-size: 14px;
  position: relative;
}
.cw-gantt-wrapper div {
}
.cw-gantt-wrapper table {
  border-collapse: collapse;
  background-color: #fff;
  border: 1px solid #ccc;
}
.cw-gantt-wrapper table td {
  border: 1px solid #ccc;
}
.cw-gantt-wrapper table td div {
  line-height: 100%;
}
.cw-gantt-task-title {
  font-weight: bold;
  height: 200%;
  width: 200px;
  padding: 4px;
}
.cw-gantt-month {
  padding-left: 5px;
  font-weight: bold;
}
.cw-gantt-today {
  background-color: #fdfde0;
}
.cw-gantt-weekend {
  font-family: tahoma, arial, verdana;
  background-color: #f5f5f5;
  text-align: center;
}
.cw-gantt-day {
  font-family: tahoma, arial, verdana;
  text-align: center;
}
.cw-gantt-task {
  border-top: 1px solid #cacaca;
  border-bottom: 1px solid #cacaca;
  height: 14px;
  background-color: #aaa8cb;
}
.cw-gantt-task-resource {
  margin: -3px 0 0 4px;
  font-size: 14px;
}
.cw-gantt-task-text {
  font-size: 14px;
}
.cw-gantt-progress {
  background-color: #fff;
  height: 2px;
  overflow: hidden;
  margin-top: 6px;
}



